"use client";
import { useState } from "react";
import Link from "next/link";

export default function VistaEquipos() {
  const [ciclistas, setCiclistas] = useState([]);
  const [nuevoCiclista, setNuevoCiclista] = useState({
    nombre: "",
    fechaInicio: "",
    fechaFin: "",
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNuevoCiclista({ ...nuevoCiclista, [name]: value });
  };

  const agregarCiclista = () => {
    if (nuevoCiclista.nombre && nuevoCiclista.fechaInicio && nuevoCiclista.fechaFin) {
      setCiclistas([...ciclistas, nuevoCiclista]);
      setNuevoCiclista({ nombre: "", fechaInicio: "", fechaFin: "" });
    } else {
      alert("Por favor completa todos los campos.");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-50 p-12">
      <div className="bg-gray-100 p-12 rounded-xl shadow-2xl w-full max-w-6xl space-y-10">
        <h1 className="text-3xl font-bold text-black">Vista Equipos</h1>

        {/* Formulario */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          {/* Campo de búsqueda de equipo */}
          <div className="flex items-center space-x-4">
            <input
              type="text"
              placeholder="Buscar equipo"
              className="flex-1 p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
            />
            <Link
              type="button"
              className="group/button relative inline-flex items-center justify-center overflow-hidden rounded-md bg-blue-600 backdrop-blur-lg px-6 py-2 text-base font-semibold text-white transition-all duration-300 ease-in-out hover:scale-110 hover:shadow-xl hover:shadow-gray-600/50 border border-white/20"
            >
              Agregar Equipo
            </Link>
          </div>

          {/* País (debajo del campo de buscar equipo) */}
          <div className="col-span-2">
            <label className="block text-lg font-medium mb-2 text-black">País</label>
            <input
              type="text"
              placeholder="País"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
            />
          </div>

          {/* Director */}
          <div className="col-span-2 sm:col-span-1">
            <label className="block text-lg font-medium mb-2 text-black">Director</label>
            <input
              type="text"
              placeholder="Director"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
            />
          </div>
        </div>

        {/* Botón de agregar ciclista mejorado */}
        <div className="flex justify-end mb-4">
          <button
            onClick={agregarCiclista}
            className="px-6 py-3 bg-green-500 text-white rounded-lg shadow-lg hover:bg-green-600 focus:outline-none"
          >
            Agregar Ciclista
          </button>
        </div>

        {/* Tabla de ciclistas */}
        <div className="overflow-x-auto">
          <table className="w-full border-collapse border border-gray-300 text-black">
            <thead>
              <tr className="bg-gray-200">
                <th className="border border-gray-300 p-4 text-left font-semibold">Ciclista</th>
                <th className="border border-gray-300 p-4 text-left font-semibold">Fecha Inicio</th>
                <th className="border border-gray-300 p-4 text-left font-semibold">Fecha Fin</th>
              </tr>
            </thead>
            <tbody>
              {ciclistas.map((ciclista, index) => (
                <tr key={index} className="bg-white">
                  <td className="border border-gray-300 p-4">{ciclista.nombre}</td>
                  <td className="border border-gray-300 p-4">{ciclista.fechaInicio}</td>
                  <td className="border border-gray-300 p-4">{ciclista.fechaFin}</td>
                </tr>
              ))}
              {/* Fila para agregar un nuevo ciclista */}
              <tr>
                <td className="border border-gray-300 p-4">
                  <input
                    type="text"
                    name="nombre"
                    value={nuevoCiclista.nombre}
                    onChange={handleInputChange}
                    placeholder="Nombre del ciclista"
                    className="w-full p-2 border rounded-lg outline-none"
                  />
                </td>
                <td className="border border-gray-300 p-4">
                  <input
                    type="date"
                    name="fechaInicio"
                    value={nuevoCiclista.fechaInicio}
                    onChange={handleInputChange}
                    className="w-full p-2 border rounded-lg outline-none"
                  />
                </td>
                <td className="border border-gray-300 p-4">
                  <input
                    type="date"
                    name="fechaFin"
                    value={nuevoCiclista.fechaFin}
                    onChange={handleInputChange}
                    className="w-full p-2 border rounded-lg outline-none"
                  />
                </td>
                <td>
                  <button
                    type="button"
                    onClick={agregarCiclista}
                    className="p-2 bg-green-500 text-white rounded-lg shadow hover:bg-green-600"
                  >
                    +
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
